'use client'

import { useState, useEffect } from 'react'
import Link from 'next/link'
import { usePathname } from 'next/navigation'
import { 
  Zap, 
  Server, 
  Key, 
  BarChart3,
  DollarSign,
  Activity,
  Webhook,
  LogOut,
  AlertCircle,
  Gauge,
  Settings,
  Calendar
} from 'lucide-react'
import { TooltipProvider } from '@/components/OnboardingTooltip'

export default function DashboardLayout({
  children,
}: {
  children: React.ReactNode
}) {
  const pathname = usePathname()
  const [apiKey, setApiKey] = useState<string | null>(null)
  const [checking, setChecking] = useState(true)

  useEffect(() => {
    if (typeof window !== 'undefined') {
      const storedKey = localStorage.getItem('api_key')
      setApiKey(storedKey)
      setChecking(false)
    }
  }, [])

  const navItems = [
    { href: '/dashboard', label: 'Endpoints', icon: Server, exact: true, tourId: 'endpoints-nav' },
    { href: '/dashboard/metered', label: 'Metered', icon: DollarSign, tourId: 'metered-nav' },
    { href: '/dashboard/usage', label: 'Usage', icon: Gauge, tourId: 'usage-nav' },
    { href: '/dashboard/agents', label: 'x402 Agents', icon: Zap, tourId: 'agents-nav' },
    { href: '/dashboard/analytics', label: 'Analytics', icon: BarChart3 },
    { href: '/dashboard/monitoring', label: 'Monitoring', icon: Activity },
    { href: '/dashboard/cron-jobs', label: 'Scheduled Jobs', icon: Calendar },
    { href: '/dashboard/webhooks', label: 'Webhooks', icon: Webhook },
    { href: '/dashboard/keys', label: 'API Keys', icon: Key, tourId: 'settings-nav' },
  ]

  const isActive = (href: string, exact?: boolean) => {
    if (exact) {
      return pathname === href
    }
    return pathname?.startsWith(href)
  }

  if (checking) {
    return (
      <div className="min-h-screen bg-gray-950 text-white flex items-center justify-center">
        <div className="text-center">
          <div className="inline-block animate-spin rounded-full h-8 w-8 border-b-2 border-lime-400"></div>
          <p className="mt-4 text-gray-400">Loading...</p>
        </div>
      </div>
    )
  }

  if (!apiKey) {
    return (
      <div className="min-h-screen bg-gray-950 text-white flex items-center justify-center">
        <div className="max-w-md w-full p-8 bg-gray-900 rounded-lg border border-gray-800">
          <AlertCircle className="w-12 h-12 text-yellow-400 mx-auto mb-4" />
          <h2 className="text-2xl font-bold mb-4 text-center">API Key Required</h2>
          <p className="text-gray-400 mb-6 text-center">
            Please set your API key to access the dashboard.
          </p>
          <div className="space-y-4">
            <input
              type="text"
              placeholder="Enter your API key (nf_live_...)"
              className="w-full px-4 py-2 bg-gray-800 border border-gray-700 rounded-lg text-white placeholder-gray-500 focus:outline-none focus:border-lime-400"
              onKeyDown={(e) => {
                if (e.key === 'Enter') {
                  const value = (e.target as HTMLInputElement).value
                  if (value) {
                    localStorage.setItem('api_key', value)
                    setApiKey(value)
                  }
                }
              }}
            />
            <button
              onClick={() => {
                const input = document.querySelector('input') as HTMLInputElement
                if (input?.value) {
                  localStorage.setItem('api_key', input.value)
                  setApiKey(input.value)
                }
              }}
              className="w-full px-4 py-2 bg-lime-400 text-gray-900 rounded-lg font-medium hover:bg-lime-300 transition-colors"
            >
              Set API Key
            </button>
            <p className="text-xs text-gray-500 text-center">
              Don't have an API key?{' '}
              <Link href="/dashboard/keys" className="text-lime-400 hover:text-lime-300">
                Create one here
              </Link>
            </p>
          </div>
        </div>
      </div>
    )
  }

  return (
    <TooltipProvider>
      <div className="min-h-screen bg-gray-950 text-white">
        {/* Navigation */}
        <nav className="sticky top-0 z-50 border-b border-gray-800 bg-gray-950/80 backdrop-blur-xl">
          <div className="max-w-7xl mx-auto px-4">
            <div className="flex items-center justify-between h-16" data-tour="dashboard-header">
              <Link href="/" className="flex items-center gap-2">
                <div className="w-8 h-8 rounded-lg bg-lime-400 flex items-center justify-center">
                  <Zap className="w-5 h-5 text-gray-900" />
                </div>
                <span className="text-lg font-bold text-white">NexFlow</span>
              </Link>
              <div className="flex items-center gap-4">
                {apiKey && (
                  <span className="text-sm text-gray-400">
                    {apiKey.substring(0, 20)}...
                  </span>
                )}
                <button
                  onClick={() => {
                    localStorage.removeItem('api_key')
                    window.location.reload()
                  }}
                  className="flex items-center gap-2 px-4 py-2 text-sm text-gray-400 hover:text-white transition-colors"
                >
                  <LogOut className="w-4 h-4" />
                  Logout
                </button>
              </div>
            </div>
          </div>
        </nav>

        <div className="max-w-7xl mx-auto px-4 py-8">
          <div className="flex gap-8">
            {/* Sidebar */}
            <aside className="w-64 flex-shrink-0">
              <nav className="space-y-1">
                {navItems.map((item) => {
                  const Icon = item.icon
                  const active = isActive(item.href, item.exact)
                  return (
                    <Link
                      key={item.href}
                      href={item.href}
                      data-tour={item.tourId}
                      className={`flex items-center gap-3 px-4 py-3 rounded-lg transition-colors ${
                        active
                          ? 'bg-gray-800 text-lime-400'
                          : 'text-gray-400 hover:text-white hover:bg-gray-900'
                      }`}
                    >
                      <Icon className="w-5 h-5" />
                      <span className="font-medium">{item.label}</span>
                    </Link>
                  )
                })}
              </nav>
            </aside>

            {/* Main Content */}
            <main className="flex-1 min-w-0">
              {children}
            </main>
          </div>
        </div>
      </div>
    </TooltipProvider>
  )
}

